.home__container {
  max-width: 100vw;
  width: 100%;
  max-height: 100vh;
  height: 100%;
  background-color: rgb(88, 107, 80);
}
.home__header {
  min-width: 100vw;
  width: 100%;
  background-color: beige;
  min-height: 15vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  font-weight: 700;
  text-shadow: 3px 3px 6px #000000;
}
.home__render {
  max-width: 100vw;
  min-height: 75vh;
  margin: 0px 5vw 0px 5vw;
  background-color: rgb(88, 107, 80);
}
